<template>
  <div>
    <h1>
      Hello from Product Component
    </h1>
    <button class="btn btn-primary" @click="onClick()"   >Home</button>
    <input type="text" v-model="id" >
    <button class="btn btn-primary" @click="onProductDetails()"   >ProductDetails</button>
  </div>
</template>

<script>
export default {
data(){

return{
  id:10
}
},
methods:{
  onClick(){
    this.$router.push('/');
  },
  onProductDetails(){
    this.$router.push({path:`/productdetails/${this.id}`});
  }
}
}
</script>

<style>

</style>



